.HorizontalSliderVatPanel {
    display:grid;
    grid-template-columns: 50px 110px auto 45px 10px;
    margin-bottom:0px;
    padding-bottom:0px;
}

.HorizontalSliderVatPanelTitle {
    display:inline-block;
    /*width:110px;*/
    position:relative;
    top:18px;
}

.HorizontalSliderVatPanelImage {
    display:inline-block;
    width:50px;
    height:50px;
}

@media screen and (max-width:500px) {
    .HorizontalSliderVatPanel {
        display:grid;
        grid-template-columns: 25px 60px auto 23px 30px;
        margin-bottom:0px;
        padding-bottom:0px;
    }    
    .HorizontalSliderVatPanelImage {
        width:25px;
        height:25px;
    }
    .HorizontalSliderVatPanelTitle { 
        height:25px;
        top:3px;
        font-size:10pt;
    }
}

.HorizontalSlider {
    display:inline-block;
    vertical-align: top;
    position:relative;
}

.HorizontalSliderBar {
    display:inline-block;
    /*position:relative;*/
    /*top:-25px; */
    vertical-align:top;

    /* width: 1000px; */ /* If you change this value you need to update constatns in Config.js */
    width:100%;

    height: 40px;
    background-color: #CCCCCC;
    text-align: center;
    margin: 0;
    padding: 0;
    border: solid 1px #888888;
    z-index: 1;
    background-image: url(data:image/png;base64,);
    background-size: 100% 100%;
    background-position: 0px 0px;
    background-repeat: no-repeat;
}

.HorizontalSliderButton {
	width: 100px; /* If you change this value you need to update constatns in Config.js */
	height: 50px;
    top: -4px;
    left:0px;
	background-color: #0088AA;
	border: solid 1px #888888;
	border-radius: 8px;
	position: absolute;
	z-index: 2;
}

.HorizontalSliderButtonWindow {
	width: 80px;
	height: 29px;
	margin: 9px;
	background-color: #000000;
	border-radius: 4px;
	z-index: 3;
	color: #FFFFFF;
	font-size: 14pt;
	text-align: center;
	font-family: 'Verdana', Geneva, sans-serif;
    font-weight: 600;
    padding-top:5px;
}

.no-select {
	-webkit-touch-callout: none; /* iOS Safari */
	-webkit-user-select: none; /* Safari */ /* Konqueror HTML */
	-moz-user-select: none; /* Firefox */
	-ms-user-select: none; /* Internet Explorer/Edge */
	user-select: none;
}

.pointer_cursor {
    cursor:pointer;
}


@media screen and (max-width:500px) {
    .HorizontalSliderBar {
        height:20px;
    }
    .HorizontalSliderButton {
        width:50px;
        height:25px;
        padding-top:0px;
        padding-left:2px;
    }
    .HorizontalSliderButtonWindow {
        width:40px;
        height:15px;
        margin-left:3px;
        margin-top:2px;
        font-size:9pt;
    }
}


